import React, {Fragment, useState} from 'react';
import Welcome from './views/Welcome'
import Profile from './views/Profile'
import Homepage from './views/Homepage'
import Stranger from './views/Stranger'
import Chat from './views/Chat'
import Search from './views/Search'
import withStyles from '@material-ui/core/styles/withStyles'
import Navbar from './MyComponents/Navbar'
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import withLogin from './HOCs/withLogin.js'

const styles={
   main:{
     padding:'0'
   }
}
 
 
const App=(props)=>{
  
  const {classes}=props
  
  const [login, setLogin]=useState(false)
  

  return (
    <Fragment>
      <div className={classes.main}>
        <BrowserRouter basename="/book">
          <Fragment>
            <Navbar login={login} setLogin={setLogin}/>
      
            <Switch>
              <Route  path="/" exact
                      render={(routeProps) => (
                      <Welcome {...routeProps} setLogin={setLogin } />
                      )}
              />
              <Route  path="/user" exact
                      render={(routeProps) => (
                      <HomepageProtected setLogin={setLogin} {...routeProps} />
                      )}
              />
              <Route path="/user/profile" exact
                     render={(routeProps) => (
                     <Profile {...routeProps} login={login } />
                     )}
              />
              <Route  path="/user/chat" exact
                      render={(routeProps) => (
                      <Chat {...routeProps} login={login } />
                     )}
              />
              <Route  path="/user/lib" exact
                      render={(routeProps) => (
                      <Stranger {...routeProps} login={login } />
                      )}
              />

              <Route  path="/user/search/:name" exact
                      render={(routeProps) => (
                      <Search {...routeProps} login={login } />
                      )}
              />
            </Switch>
          </Fragment>
        </BrowserRouter>
      </div>
    </Fragment>
  );
}


const HomepageProtected=withLogin(Homepage)




export default withStyles(styles)(App);
