import React from 'react'
import withStyles from '@material-ui/core/styles/withStyles'
import PersonalInfo from '../MyComponents/PersonalInfo'
import Unread from '../MyComponents/Unread'
import Book from '../MyComponents/Book'
import {Grid, Paper, Typography} from '@material-ui/core'
import {Redirect } from 'react-router-dom'
import Recently from '../MyComponents/Recently'
import withData from '../HOCs/withData.js'

const styles={
  root:{
    padding:"2rem",
    margin:"1em"
  }, 
  fixedSide:{
    position:'static' 
  }
}

const Homepage=props=>{
  const {classes}=props
  if(props.login){
    props.setLogin(true)
  }
  return(
    <Grid container>
      {!props.login&&<Redirect to='/'/>}
      <Grid item md={1}></Grid>
      <Grid item md={10} sm={12}>
        <Grid container spacing={8} >
          <Grid item md={8} xs={12}>
            <Recently token={props.token}/>
          </Grid>
          <Grid item md={4} sm={12}>
            <div className={classes.fixedSide}>
              <Paper className={classes.root}>
                <PersonalInfo />
              </Paper>
              <Paper className={classes.root}>
                <Unread />
              </Paper>
            </div>
          </Grid>
        </Grid>

      </Grid>
      <Grid item md={1}></Grid>
    </Grid>
  )
}
const recentConfig={url:'http://localhost/api/books/all/'}
const recentlyWithData=withData(Recently, recentConfig)

export default withStyles(styles)(Homepage)