import React,{Fragment} from 'react'
import withStyles from '@material-ui/core/styles/withStyles'
import PersonInfo from '../MyComponents/PersonInfo'
import Library from '../MyComponents/Library/Library';
import {Paper, Grid} from '@material-ui/core';

const styles={
    root:{
        padding:'2rem'
    }
}

const Search=props=>{
  const {classes}=props  
  return(
      <Grid container>
      <Grid item md={1}></Grid>
      <Grid item md={10}>
        <Paper className={classes.root}>
          <PersonInfo own={false} name={props.match.params.name}/>
          <Library/>
        </Paper>  
      </Grid>  
      <Grid item md={1}></Grid>    
      </Grid>
  )
}
 
export default withStyles(styles)(Search)