import React from 'react'

// This function takes a component...
/*
config={
	url,
	method,
	allowed,
}

upConfig={
	method,
	allowed
}

*/
function withLogin(WrappedComponent) {
    // ...and returns another component...
    return class extends React.Component {
      constructor(props) {
        super(props);
        this.isLoggedIn = this.isLoggedIn.bind(this);
        this.setLogin = this.setLogin.bind(this);
        this.state = {
          login:false,
          token:''
        };
      }
  
      componentWillMount() {
        // ... that takes care of the subscription...
        this.isLoggedIn()
      }
      setLogin(boolean){
        if(!boolean){
          document.cookie = "token=haha; expires = Thu, 01 Jan 1970 00:00:00 GMT;path=/";
          this.setState({login:false})
        }
      }
  	  isLoggedIn(){
        
  	  	const raw=document.cookie.split(';')
      	raw.forEach((value, index)=>{
        	const array=value.split('=')
        	if(array[0].trim()=='token'){
         	 	const tn=array[1].split(',')[0]
          		this.setState({login:true, token:tn})
         	}
      	})
      	
  	  }

      render() {
        // ... and renders the wrapped component with the fresh data!
        // Notice that we pass through any additional props
        return <WrappedComponent login={this.state.login} token={this.state.token} setLogin={this.setLogin} {...this.props}/>;
      }
    };
  }
  export default withLogin